<script>
export default {
  name: 'ImgPreview',
  data () {
    return {
      visible: false,
      clientWidth: 0, // 图片容器宽度
      list: [], // 图片列表
      scale: 1, // 图片初始缩放比例
      rotate: 0, // 图片初始旋转比例
      translateX: 0, // 图片盒子初始水平平移距离
      active: 0 // 当前活动的图片
    }
  },
  mounted () {
    this.clientWidth = document.body.offsetWidth + 'px'
    document.body.addEventListener('mousewheel', this.mousewheelZoom)
  },
  beforeDestroy () {
    document.body.removeEventListener('mousewheel', this.mousewheelZoom)
  },
  methods: {
    show () {
      this.visible = true
    },
    // 切换图片
    changeImg (dir) {
      if (dir > 0 && this.active === this.list.length - 1) {
        return
      }
      if (dir < 0 && this.active === 0) {
        return
      }
      if (dir > 0 && this.active < this.list.length - 1) {
        this.active += 1
      } else if (dir < 0 && this.active > 0) {
        this.active -= 1
      }
      this.scale = 1
      this.translateX = -100 * this.active
      this.setImgTransform()
      this.$refs.imgBox.style.transform = 'translateX(' + this.translateX + '%)'
    },
    // 鼠标滚轮缩放图片
    mousewheelZoom (arg) {
      const dir = (arg.detail || -arg.wheelDelta) > 0 ? -1 : 1
      this.zoomImg(dir)
    },
    // zoom图片
    zoomImg (dir) {
      this.scale = dir > 0 ? this.scale + 0.2 : dir < 0 ? this.scale - 0.2 : 1
      if (this.scale < 0.4) {
        this.scale = 0.4
        return
      } else if (this.scale > 3.2) {
        this.scale = 3.2
        return
      }
      this.setImgTransform()
    },
    // 翻转图片
    rotateImg (dir) {
      this.rotate = dir > 0 ? this.rotate - 90 : this.rotate + 90
      this.setImgTransform()
    },
    // 还原图片
    reset () {
      this.scale = 1
      this.rotate = 0
      this.setImgTransform()
    },
    // 设置图片的transform
    setImgTransform () {
      this.$refs.img[this.active].style.transform = 'rotateZ(' + this.rotate + 'deg) scale(' + this.scale + ')'
    },
    // 下载图片(利用canvas处理跨域图片直接打开图片的问题)
    downloadImg () {
        try {
            let image = new Image()
            image.crossOrigin = 'anonymous';
            image.onload = () => {
                let canvas = document.createElement('canvas')
                canvas.width = image.width
                canvas.height = image.height
                let context = canvas.getContext('2d')
                context.drawImage(image, 0, 0, image.width, image.height)
                let url = canvas.toDataURL('image/png')
                let a = document.createElement('a')
                let event = new MouseEvent('click')
                a.download = '';
                a.href = url
                a.dispatchEvent(event)
            }
            image.onerror = (err) => {
                console.log('图片下载失败')
            }
            image.src = this.list[this.active]
        } catch (err) {
            console.log('图片下载失败')
        }
    },
  }
}
</script>

<style lang="less">
.preview-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: rgba(0, 0, 0, 0.6);
  .preview-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
  }
  .close{
      position: fixed;
      top: 20px;
      right: 20px;
      cursor: pointer;
      z-index: 10000;
  }
  .img-wrapper {
    width: 100%;
    height: calc(100% - 50px);
    overflow: hidden;
    .img-box {
      width: 100%;
      height: 100%;
      transition: all 0.5s;
    }
    .empty{
        font-size: 22px;
        color: #fff
    }
    .img-list {
      width: 10000%;
      height: 100%;
      display: flex;
    }
    .img-item {
        display: flex;
        justify-content: center;
        align-items: center;
        align-content: center;
      width: 100%;
      height: 100%;
      text-align: center;
      position: relative;
      overflow: hidden;
      img {
        max-width: 100%;
        max-height: 100%;
        transform-origin: center;
        transition: all 0.5s;
      }
    }
  }
  .img-tools {
    text-align: center;
    color: #fff;
    .action {
      display: inline-block;
      margin: 10px;
      cursor: pointer;
    }
  }
}
</style>
 